.container{
    display: flex;
    background-color: #f8f8f8;
    border-radius: 5px;
    padding: 0.8em;
    align-items: center;
    margin-bottom: 1em;
  }
  
  .label-avatar{
    width: 280px;
    height: 280px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
  }
  
  .label-avatar input{
    display: none;
  }
  
  .label-avatar span{
    z-index: 99;
    position: absolute;
    opacity: 0.7;
    transition: all 0.5s;
  }
  
  .label-avatar span:hover{
    opacity: 1;
    transform: scale(1.4);
  }
  
  .form-profile img{
    margin-bottom: 1em;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .form-profile label{
    margin-bottom: 0.5em;
    font-size: 1em;
  }
  
  .form-profile input, textarea, select{
    margin-bottom: 1em;
    padding: 0.7em;
    border:0;
    border-radius: 5px;
    max-width: 300px;
  }
  
  .form-profile input:disabled{
    cursor: not-allowed;
  }
  
  .logout-btn{
    padding: 8px 20px;
    border: 1px solid #121212;
    background-color: transparent;
    border-radius: 5px;
    font-size: 1em;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .botaoPerfil{
    height: 30px;
    border:0;
    border-radius: 20px;
    background-color: #5EA1BD;
    color: #FFF;
    font-size: 1rem;
    transition: 0.7s;
    max-width: 120px;
    margin-top: 5px;
  }

  .botaoPerfil:hover{
    background-color: #5ea0bdc2;
  }

  .botaoLogout{
    height: 30px;
    border:0;
    background-color: transparent;
    border-radius: 50px;
    color: #181c2e;
    font-size: 1rem;
    transition: 0.7s;
    width: 35px;
    margin-top: 5px;
    transition: 1s;
  }

  .botaoLogout:hover{
    background-color: #8e2525;
    color: #FFF;
  }

  .icone{
    font-size: 1.3rem;
  }



  .profile-fields {
    display: flex;
    flex-wrap: wrap;
    gap: 1em; /* Espaçamento entre os itens */
  }
  
  .field {
    flex: 1 1 30%; /* Cada item ocupa 30% da largura do contêiner */
    min-width: 150px; /* Define uma largura mínima para evitar que fiquem muito estreitos */
    display: flex;
    flex-direction: column; /* Garante que a label fique acima do input */
  }
  
  .form-profile label {
    margin-bottom: 0.3em;
  }
  
  .form-profile input {
    width: 100%;
    padding: 0.7em;
    border: 0;
    border-radius: 5px;
  }

  .btn-disponibilidade {
    margin-top: 10px;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.btn-disponibilidade.disponivel {
    background-color: #28a745;
    color: white;
}

.btn-disponibilidade.indisponivel {
    background-color: #d9534f;
    color: white;
}

.btn-disponibilidade:hover {
    opacity: 0.9;
}

  
  

  @media screen and (max-width: 350px){
    .form-profile img{
      margin-bottom: 1em;
      border-radius: 50%;
      object-fit: cover;
      width: 60vw;
      height: 60vw;
    }
    .label-avatar{
      width: 70vw;
      height: 70vw;
    }
  }
  